.sidebar-col {
  position: fixed; /* Fija el sidebar para que se superponga al contenido principal */
  top: 0;
  left: 0;
  min-height: 100vh;
  width: 200px;
  background-color: #343a40;
  z-index: 1000;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Centra verticalmente el contenido del sidebar */
}

.sidebar-col.hidden {
  transform: translateX(-100%); /* Mueve el sidebar fuera de la pantalla cuando est√° oculto */
}

.toggle-sidebar-col {
  position: absolute;
  top: 50%; /* Posiciona el bot√≥n en el centro vertical del sidebar */
  left: 200px; /* Posiciona el bot√≥n a la derecha del sidebar cuando est√° visible */
  transform: translateY(-50%); /* Ajusta la posici√≥n para centrar verticalmente */
  z-index: 1001;
  transition: left 0.3s ease;
}

.toggle-sidebar-col.hidden {
  left: 0; /* Mueve el bot√≥n al borde izquierdo cuando el sidebar est√° oculto */
}

.toggle-sidebar-button {
  width: 50px;
  height: 50px;
  background-color: #343a40;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
}

.toggle-sidebar-button:hover {
  background-color: #555;
}

/* Comportamiento para pantallas peque√±as */
@media (max-width: 767px) {
  .sidebar-col {
    width: 200px;
    transform: translateX(0); /* Asegura que el sidebar est√© visible al inicio */
  }

  .sidebar-col.hidden {
    transform: translateX(-100%); /* Mueve el sidebar fuera de la pantalla cuando est√° oculto */
  }

  .toggle-sidebar-col {
    left: 200px;
  }

  .toggle-sidebar-col.hidden {
    left: 0;
  }
}

/* Comportamiento para pantallas grandes */
@media (min-width: 993px) {
  .sidebar-col {
    min-width: 200px;
    transform: translateX(0); /* Asegura que el sidebar est√© visible */
  }

  .toggle-sidebar-col {
    left: 200px; /* Ajusta la posici√≥n del bot√≥n al lado del sidebar */
  }

  .toggle-sidebar-col.hidden {
    left: 0;
  }
}

button.sidebar-toggle:hover {
  background-color: #495057;
}

.layout-topbar {
  background: #eef2f7;
  border-bottom: 1px solid rgba(0,0,0,.06);
  padding-top: 6px;
  padding-bottom: 6px;
}

:root{
  --navbar-h: 70px;     /* la misma que us√°s en marginTop del contenido */
}

/* Columna del sidebar (escritorio) */
.sidebar-col{
  background: #343a40;
  color: #fff;
  min-width: 200px;
  padding-left: 0;
  padding-right: 0;
}

/* Hace que el sidebar no ‚Äúdesborde‚Äù y scrollee dentro */
.sidebar-col .sidebar-scroll{
  height: calc(100vh - var(--navbar-h));
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

/* (Opcional) ocultar barra horizontal global si algo empuja */
html, body{
  overflow-x: hidden;
}