/* Estilos para el select personalizado */
.custom-style-select {
    border: 1px solid transparent; /* Borde transparente */
    background-color: rgba(255, 255, 255, 0.7); /* Fondo inicial, ajusta seg√∫n sea necesario */
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1); 
    /* Sombra similar a la de los Form.Control */
    transition: border-color 0.2s, background-color 0.2s; /* Transici√≥n suave */
  
    &:focus {
      border-color: #80bdff; /* Cambia el color del borde al seleccionar */
      background-color: white; /* Cambia el fondo al seleccionar */
    }
  }

  /* Aseg√∫rate de agregar este CSS al archivo de estilos */

.brand-text {
  font-size: 1.5rem; /* Tama√±o por defecto */
}

.welcome-text {
  font-size: 1.2rem; /* Tama√±o por defecto */
}

/* Ajustes para pantallas peque√±as */
@media (max-width: 993px) {
  .brand-text {
    font-size: 1.2rem; /* Reduce el tama√±o del logo en pantallas peque√±as */
  }

  .welcome-text {
    font-size: 1rem; /* Reduce el tama√±o del texto de bienvenida */
  }
}

@media (max-width: 576px) {
  .brand-text {
    font-size: 1rem; /* M√°s peque√±o para pantallas extra peque√±as */
  }

  .welcome-text {
    font-size: 0.9rem; /* Ajuste a√∫n menor para texto de bienvenida */
  }
}

.truncated-text {
  max-width: 200px; /* Puedes ajustar el tama√±o m√°ximo seg√∫n tus necesidades */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.my-input-date {
  width: 150px; /* Ancho ajustado para YYYY-MM-DD */
  min-width: 150px;
